.addtodo_overlay {
    display: flex;
    margin: 10px auto;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 1200px;
    padding: 0 20px;
}
.addtodo_form {
    display: flex;
    margin: 0 auto;
}
.addtodo_form input {
    font-size: 16px;
    padding: 10px 10px 5px 15px;
    width: 45vw;
    max-width: 600px;
    margin-right: 40px;
    border: none;
    border-bottom: 2px solid darkslategray;
}

.addtodo_form button {
    font-size: 16px;
    padding: 5px 10px;
    padding-right: 15px;
    margin-left: auto;
    border: none;
    background-color: darkslategray;
    color: whitesmoke;
    text-transform: uppercase;
    font-weight: 600;
    transition: background-color 0.5s;
}
@media (min-width: 320px) {
    .addtodo_form button {
        font-size: 12px;
    }
    .addtodo_form input {
        margin-right: 15px;
        font-size: 12px;
    }
}
@media (min-width: 992px) {
    .addtodo_form button {
        font-size: 14px;
    }
    .addtodo_form input {
        margin-right: 40px;
        width: 35em;
        font-size: 14px;
    }
}
.addtodo_form button:hover {
    background-color: teal;
    cursor: pointer;
}