.todolist_overlay{
    margin: 0 auto;
    padding: 0 20px; 
}
.todolist {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    margin: 0 auto;
    text-align: left;
    max-width: 600px;
}
.todolist_item {
    text-transform: uppercase;
    padding: 5px 10px;
    margin: 0;
    margin-bottom: 10px;
    list-style-type: none; 
    border: 2px solid tomato;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.todolist_item:hover {
    cursor: pointer;
}
.todolist_item.active {
    color: green;
    border-color: darkgreen;
}
.remove_item {
    color: tomato;
}